"use client";

import { useState } from "react";
import { MessageCircle, X } from "lucide-react";
import { TodoChatbot } from "../todo/ChatBot";

export function ChatbotIcon() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      {!isOpen && (
        <button
          onClick={() => setIsOpen(true)}
          className="fixed bottom-6 right-6 bg-primary hover:bg-primary/90 text-primary-foreground p-4 rounded-full shadow-lg hover:shadow-xl transition-all duration-200 z-40 flex items-center justify-center"
          title="Open Todo Assistant"
        >
          <MessageCircle className="h-6 w-6" />
        </button>
      )}

      {/* Chat Dialog */}
      {isOpen && (
        <div className="fixed bottom-6 right-6 z-50 w-96 max-h-[600px] rounded-lg shadow-2xl overflow-hidden bg-background">
          <TodoChatbot onClose={() => setIsOpen(false)} />
        </div>
      )}
    </>
  );
}